@import url('https://fonts.googleapis.com/css2?family=Harmattan&display=swap');
@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.0.2/dist/css/bootstrap.min.css');

body {
  font-size: 40px;
  font-family: 'Harmattan', sans-serif;
  direction: ltr; }

#app {
  max-width: 980px;
  margin: auto;
  padding: 0 15px;
  text-align: center; }

.container {
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 0 20px -5px #555;
  max-width: 350px;
  padding: 20px;
  text-align: left;
  margin: 50px auto; }

h1 {
  text-align: center;
  border-bottom: 1px solid #bbb;
  padding-bottom: 10px; }

form input {
  border-radius: 1rem !important; }

button {
  font-size: 1.25rem !important;
  border-radius: 1.25rem !important; }

p#result {
  text-align: center;
  margin: 10px 0 0 0; }

p#result:empty {
  display: none; }

label {
  font-size: 30px; }

@media (min-width: 768px) {
  .container {
    position: absolute;
    margin: auto;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%); } }
